.eventTable {
    border-radius: 15px;
    position: relative;
    overflow-x: auto;
    overflow-y: auto;
    // max-height: calc(100vh - 372px);
    width: 100%;
    color: #313567;
    margin-top: 20px;
    margin-bottom: 49.7344px;

    &_noDataFound {
        display: flex;
        justify-content: center;
        align-items: center;
        height: calc(100vh - 350px);
    }


    table {
        border-collapse: collapse;
        border-spacing: 0;
        width: 100%;
        overflow-y: scroll;
        max-height: 400px;
        white-space: nowrap;
    }


    th {
        position: sticky;
        top: -10px;
        z-index: 99;
        background-color: #ffffff;
        white-space: nowrap;
    }

    th,
    td {
        text-align: left;
        padding: 10px 0px;
        border-bottom: 1px solid rgba(128, 128, 128, 0.589);
        padding-right: 20px;
        font-size: 12px;
    } 

    &_rightRow {
        text-align: right !important;
    }

    &_view {
        border-radius: 20px;
        padding: 5px 36px;
        font-size: 15px;
        border: none;
        color: #e5e0e0;
        font-weight: 400;
        background: #3686FF
    }
    &_view:hover {
        background: #1b417a

    }

    &_detail {
        border-radius: 20px;
        padding: 5px 36px;
        font-size: 15px;
        border: none;
        color: #e5e0e0;
        font-weight: 400;
        background-image: linear-gradient(to right, #3985FC, #ED624F);
    }
    &_detail:hover {
        background-image: linear-gradient(to right, #193b72, #632821);
    }

    &_location {
        color: #ff3131;

    }

}